import contact from "./Contact.module.css"

function Contact(props){
    return(
        <div id='contact' className={contact.contact}>
            <div className={contact.title}>
                <img src="https://cdn-icons-png.flaticon.com/128/2967/2967878.png" alt=""/>
                <h1 className={contact.contactTitle}>Get In Touch</h1>
            </div>
            
            <div className={contact.contactSection}>
            {
                    props.contact.map((contactDetail)=>(
                        <div className={contact.contactDetail}>
                            <img alt={contactDetail.name} src={contactDetail.icon}/>
                            <p>{contactDetail.name}</p>
                        </div>
                    ))
                }
            </div>
        
        </div>
    )
}

export default Contact;