import intro from "./Introduction.module.css";
import AnchorLink from "react-anchor-link-smooth-scroll";
import ParticlesComponent from "../ParticlesBackground/particles";
import { pdfjs } from 'react-pdf';
import Modal from "../../ExtraComponents/Modal/Modal";
import { motion } from "framer-motion";

pdfjs.GlobalWorkerOptions.workerSrc = new URL(
    'pdfjs-dist/build/pdf.worker.min.mjs',
    import.meta.url,
  ).toString();

  const textVarient ={
    initial:{
        x:-500,
        opacity: 0,
    },
    animate:{
        x: 0,
        opacity: 1,
        transition:{
            duration: 1,
            staggerChildren: 0.1
        }
    }
  }

function Intro(){

    return(
        <div id='home' className={intro.intro}>
            <ParticlesComponent className={intro.particles} />
        <div className={intro.imgDiv}><motion.img initial={{ opacity: 0, scale: 0 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{
                duration: 3,
                scale: { type: "spring", visualDuration: 0.4, bounce: 0.5 },
            }}
            alt="Shreya" src={process.env.PUBLIC_URL + "/images/profilePic.JPEG"} className={intro.introImg}/></div>
            <div className={intro.info}>
            <motion.h1 variants={textVarient} initial="initial" animate="animate">
                <span>I'm Shreya Redkar,</span> A Full-Stack Developer</motion.h1>
            <motion.p variants={textVarient} initial="initial" animate="animate">
                With 3+ years of expertise in creating scalable web applications and backend solutions.</motion.p>
            <motion.div className={intro.action} variants={textVarient} initial="initial" animate="animate">
                <div className={intro.connect}><AnchorLink className={intro.anchorLink} offset={50} href="#contact">Connect with me</AnchorLink></div>
                <div className={intro.resume}> <Modal name="Check out resume"/> </div>
            </motion.div>
            </div>
        </div>
    )
}

export default Intro;