import ski from "./Skills.module.css"
function Skills(props){
    console.log("skills: ", props.skills)
    return(
        <div id='skills' className={ski.skillsSet}>
            <div className={ski.title}>
                <img src="https://cdn-icons-png.flaticon.com/128/10262/10262300.png" alt=""/>
                <h1 className={ski.skillTitle}>Skills</h1>
            </div>

            <div className={ski.skillDisplay}>
                {
                    props.skills.map((skill)=>(
                        <div className={ski.skills}>
                            <img src={skill.icon} alt="skill icon"/>
                            <span>{skill.name}</span>
                        </div>
                        
                    ))
                }
            </div>

        </div>
    )
}

export default Skills;