import React, { useState } from "react";
import modalCSS from "./Modal.module.css";
import PDFComponent from "../PDFComponent/PDFComponent";

export default function Modal(props) {
  const [modal, setModal] = useState(false);

  const toggleModal = () => {
    setModal(!modal);
  };

  if(modal) {
    document.body.classList.add('active-modal')
  } else {
    document.body.classList.remove('active-modal')
  }

  return (
    <>
      <span onClick={toggleModal}>
        {props.name}
      </span>

      {modal && (
        <div className={modalCSS.modal}>
          <div onClick={toggleModal} className={modalCSS.overlay}></div>
          <div className={modalCSS.modalContent}>
            {/* <h3>{props.name}</h3> */}
            <PDFComponent/>
            {/* <p>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident
              perferendis suscipit officia recusandae, eveniet quaerat assumenda
              id fugit, dignissimos maxime non natus placeat illo iusto!
              Sapiente dolorum id maiores dolores? Illum pariatur possimus
              quaerat ipsum quos molestiae rem aspernatur dicta tenetur. Sunt
              placeat tempora vitae enim incidunt porro fuga ea.
            </p> */}
            <div className={modalCSS.closeModal} onClick={toggleModal}>
            <img src="https://cdn-icons-png.flaticon.com/128/5369/5369422.png" alt=""/>  
            </div>
          </div>
        </div>
      )}
    </>
  );
}