import edu from "./Education.module.css";
import EducationBox from "./EducationBox";
import {useRef} from "react";
import {motion, useScroll, useTransform} from "framer-motion";

function Education(props){
    const ref = useRef();
    const {scrollYProgress} = useScroll({
        target:ref,
        offset:["start start", "end start"]
    })
    const yBg = useTransform(scrollYProgress, [0,1], ["0%", "50%"]);
    const yBgStar = useTransform(scrollYProgress, [0,1], ["0%", "-50%"]);

    return(
        <div id='education' className={edu.educationModule} ref={ref}>
            <motion.div className={edu.stars} style={{x: yBgStar}}></motion.div>
            <motion.div className={edu.sun} style={{y: yBg}}></motion.div>
            <div className={edu.title}>
                <img src="https://cdn-icons-png.flaticon.com/128/18600/18600635.png" alt=""/>
                <h1 className={edu.educationTitle}>Education</h1>
            </div>
            
            <motion.div className={edu.educationBoxes} style={{y: yBg}}>
                {
                    props.education.map((institude, index)=>(
                        <EducationBox institute={institude} key={index}/>
                    ))
                }   
            </motion.div>
        </div>
    )
}

export default Education;