import about from "./About.module.css";
import {useRef} from "react";
import {motion, useScroll, useTransform} from "framer-motion";


function About(){
    const ref = useRef();
    const {scrollYProgress} = useScroll({
        target:ref,
        offset:["start start", "end start"]
    })
    const yBg = useTransform(scrollYProgress, [0,1], ["0%", "300%"]);
    const yBgPlanet = useTransform(scrollYProgress, [0,1], ["0%", "50%"]);
    const yBgIntro = useTransform(scrollYProgress, [0,1], ["0%", "100%"]);
    const yBgText = useTransform(scrollYProgress, [0,1], ["0%", "10%"]);
    const yBgtitle = useTransform(scrollYProgress, [0,1], ["0%", "-50%"]);


    return(
        <div id='about' className={about.about} ref={ref}>
            <motion.div className={about.title} style={{y: yBg}}>
            <img src="https://cdn-icons-png.flaticon.com/128/12917/12917072.png" alt=""/>
            <h1 className={about.aboutTitle}>About Me</h1>
            </motion.div>
            <motion.div className={about.mountains}></motion.div>
            <motion.div className={about.stars} style={{x: yBgPlanet}}></motion.div>
            <motion.div className={about.planets} style={{y: yBgPlanet}}></motion.div>
            <div className={about.aboutme}>
            <motion.h1>My Introduction<img className={about.icon} src="https://cdn-icons-png.flaticon.com/128/7334/7334113.png" alt=""/>
            </motion.h1>
                <motion.div className={about.intro} style={{y: yBgIntro}}>
                    {/* <img src={process.env.PUBLIC_URL + "/images/MyImage.Jpg"} alt=""/> */}
                    <p>Welcome to my digital space! Im a full-stack developer, tech enthusiast, and lifelong learner. Fueled by a passion for creating efficient and scalable solutions, I enjoy 
                     crafting everything from robust back-end systems to user-friendly front-end designs.<br/>
                     Im proficient in Java, JavaScript, Node.js, Vue.js, and React.js. 
                        My expertise extends to optimizing databases like MySQL and PostgreSQL, 
                        where Ive improved performance by up to 30%.<br/>
                        I thrive on solving complex problems and collaborating with cross-functional teams to deliver 
                        impactful results. My work in microservices architecture has increased system scalability by 25%, 
                        and Ive developed 20+ RESTful APIs that enhanced functionality and user engagement.<br/>
                        Currently, Im seeking opportunities in Dubai to contribute to innovative projects and grow within a 
                        dynamic team. Beyond coding, Im a curious learner, and a creative thinker who 
                        enjoys exploring the intersection of technology and design.
                    </p>
                 </motion.div>

                 <motion.h1 style={{y: yBgtitle}}>Personal Journey<img className={about.icon} src="https://cdn-icons-png.flaticon.com/128/7334/7334113.png" alt=""/>
                 </motion.h1>
                 <motion.div className={about.personalJourney} style={{y: yBgText}}>
                    <div className={about.slides}>
                        <div className={about.slideShow}>
                            {/* <img src={process.env.PUBLIC_URL + "/images/sketch3.jpg"} alt=""/> */}
                            <img src={process.env.PUBLIC_URL + "/images/dog.PNG"} alt=""/>
                            <img src={process.env.PUBLIC_URL + "/images/sketch1.jpg"} alt=""/>
                            <img src={process.env.PUBLIC_URL + "/images/dog2.jpg"} alt=""/>
                            <img src={process.env.PUBLIC_URL + "/images/sketch2.jpg"} alt=""/>
                        </div>
                    </div>
                    <p>Growing up in the beautiful state of Goa, I was always curious about how things work, whether 
                        it was fixing a gadget or understanding the world around me. This curiosity led me to pursue 
                        engineering, where I not only learned about technology but also discovered my passion for solving 
                        problems and creating meaningful solutions.<br/><br/>
                        Starting my professional life was an exciting time filled with opportunities to learn and grow. 
                        Early on, I worked closely with people, helping them adapt to new systems and technologies. 
                        My ability to communicate and connect with others quickly became a strength, and within a few months, 
                        I transitioned into a role where I could bridge the gap between people and processes. This was a turning point 
                        that taught me the value of empathy, collaboration, and adaptability.<br/><br/>
                        Every experience since then has been a stepping stoneteaching me to embrace challenges, 
                        celebrate successes, and keep learning. Whether it was improving systems, working on new ideas, 
                        or collaborating with diverse teams, I discovered that growth comes from stepping outside your comfort zone.<br/><br/>
                        On 9th November 2024, I took one of the biggest steps in my journeyI landed in Dubai, 
                        ready to start a new chapter. With its vibrant culture and limitless opportunities, 
                        Dubai represents hope, ambition, and the chance to build something extraordinary.<br/><br/>
                        Beyond work, I cherish moments spent exploring creative ideas, sketching on the paper, learning something new every day, 
                        or simply unwinding with my mischievous dog. To me, life is about embracing every experience, whether big or small, 
                        and finding joy in the journey. Im excited to see what this new adventure holds and how it will shape the next chapter of my story.
                        </p>
                 </motion.div>
                </div>
        </div>
    )
}

export default About;